"use client";
import { useState } from "react";
import { usePoll } from "@/contexts/PollContext";
import Navbar from "./components/Navbar";
import { usePollActions } from "@/hooks/pollAction";
import { Users, Vote, Plus, CheckCircle, AlertCircle, Crown } from "lucide-react";

export default function Home() {
  const {
    userAddress,
    isConnected,
    totalCandidates,
    isLoading,
    getNoOfCandidates,
    candidatesName,
    owner
  } = usePoll();

  const {
    vote,
    isVoting,
    isVoted,
    voteError,
    resetVote,
    addCandidate,
    isAdding,
    isAdded,
    addCandidateError,
    resetAddCandidate,
  } = usePollActions();

  const [newCandidate, setNewCandidate] = useState("");
  const [selectedCandidate, setSelectedCandidate] = useState("");

  console.log("call", getNoOfCandidates);
  console.log("üë§ Connected User:", userAddress);
  console.log("üëë Owner:", owner.data);
  console.log("üßë‚Äçü§ù‚Äçüßë Total Candidates:", totalCandidates);
  console.log("üìã Candidate Names:", candidatesName.data);

  const isOwner = userAddress === owner?.data;
  const candidatesList = Array.isArray(candidatesName?.data) ? candidatesName.data : [];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <Navbar />
      
      <div className="max-w-6xl mx-auto px-4 py-8">
        {/* Header Section */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-slate-800 mb-4">
            Decentralized Voting System
          </h1>
          <p className="text-slate-600 text-lg">
            Cast your vote securely on the blockchain
          </p>
          {isOwner && (
            <div className="inline-flex items-center gap-2 mt-4 px-4 py-2 bg-amber-100 border border-amber-200 rounded-full">
              <Crown className="w-4 h-4 text-amber-600" />
              <span className="text-amber-700 font-medium">Owner Access</span>
            </div>
          )}
        </div>

        {/* Main Content Grid */}
        <div className="grid lg:grid-cols-2 gap-8">
          {/* Candidates List */}
          <div className="bg-white rounded-xl shadow-lg border border-slate-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <Users className="w-6 h-6 text-blue-600" />
              <h2 className="text-2xl font-bold text-slate-800">Candidates</h2>
              <span className="bg-blue-100 text-blue-800 px-3 py-1 rounded-full text-sm font-medium">
                {candidatesList.length}
              </span>
            </div>

            {candidatesList.length === 0 ? (
              <div className="text-center py-8">
                <Users className="w-16 h-16 text-slate-300 mx-auto mb-4" />
                <p className="text-slate-500">No candidates registered yet</p>
              </div>
            ) : (
              <div className="space-y-3">
                {candidatesList.map((name, idx) => (
                  <div
                    key={idx}
                    className={`p-4 rounded-lg border-2 transition-all cursor-pointer hover:shadow-md ${
                      selectedCandidate === name
                        ? 'border-blue-500 bg-blue-50'
                        : 'border-slate-200 hover:border-slate-300'
                    }`}
                    onClick={() => setSelectedCandidate(name)}
                  >
                    <div className="flex items-center justify-between">
                      <span className="font-medium text-slate-800">{name}</span>
                      {selectedCandidate === name && (
                        <CheckCircle className="w-5 h-5 text-blue-600" />
                      )}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Voting Actions */}
          <div className="space-y-6">
            {/* Vote Section */}
            <div className="bg-white rounded-xl shadow-lg border border-slate-200 p-6">
              <div className="flex items-center gap-3 mb-6">
                <Vote className="w-6 h-6 text-green-600" />
                <h2 className="text-2xl font-bold text-slate-800">Cast Your Vote</h2>
              </div>

              {!isConnected ? (
                <div className="text-center py-8">
                  <AlertCircle className="w-16 h-16 text-amber-400 mx-auto mb-4" />
                  <p className="text-slate-600 mb-4">Please connect your wallet to vote</p>
                </div>
              ) : candidatesList.length === 0 ? (
                <div className="text-center py-8">
                  <p className="text-slate-600">No candidates available for voting</p>
                </div>
              ) : (
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-slate-700 mb-2">
                      Select Candidate
                    </label>
                    <input
                      type="text"
                      placeholder="Enter candidate name or select from list"
                      className="w-full p-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                      value={selectedCandidate}
                      onChange={(e) => setSelectedCandidate(e.target.value)}
                    />
                  </div>

                  <button
                    onClick={() => vote(selectedCandidate)}
                    disabled={isVoting || !selectedCandidate.trim()}
                    className="w-full bg-gradient-to-r from-blue-600 to-blue-700 text-white px-6 py-3 rounded-lg font-medium hover:from-blue-700 hover:to-blue-800 disabled:opacity-50 disabled:cursor-not-allowed transition-all transform hover:scale-105 active:scale-95"
                  >
                    {isVoting ? (
                      <div className="flex items-center justify-center gap-2">
                        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                        Voting...
                      </div>
                    ) : (
                      "Cast Vote"
                    )}
                  </button>

                  {isVoted && (
                    <div className="flex items-center gap-2 p-4 bg-green-50 border border-green-200 rounded-lg">
                      <CheckCircle className="w-5 h-5 text-green-600" />
                      <span className="text-green-800 font-medium">Vote cast successfully!</span>
                    </div>
                  )}

                  {voteError && (
                    <div className="flex items-center gap-2 p-4 bg-red-50 border border-red-200 rounded-lg">
                      <AlertCircle className="w-5 h-5 text-red-600" />
                      <span className="text-red-800">{voteError.message}</span>
                    </div>
                  )}
                </div>
              )}
            </div>

            {/* Add Candidate Section (Owner Only) */}
            {isOwner && (
              <div className="bg-white rounded-xl shadow-lg border border-slate-200 p-6">
                <div className="flex items-center gap-3 mb-6">
                  <Plus className="w-6 h-6 text-purple-600" />
                  <h2 className="text-2xl font-bold text-slate-800">Add New Candidate</h2>
                </div>

                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-slate-700 mb-2">
                      Candidate Name
                    </label>
                    <input
                      type="text"
                      placeholder="Enter candidate name"
                      className="w-full p-3 border border-slate-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all"
                      value={newCandidate}
                      onChange={(e) => setNewCandidate(e.target.value)}
                    />
                  </div>

                  <button
                    onClick={() => addCandidate(newCandidate)}
                    disabled={isAdding || !newCandidate.trim()}
                    className="w-full bg-gradient-to-r from-purple-600 to-purple-700 text-white px-6 py-3 rounded-lg font-medium hover:from-purple-700 hover:to-purple-800 disabled:opacity-50 disabled:cursor-not-allowed transition-all transform hover:scale-105 active:scale-95"
                  >
                    {isAdding ? (
                      <div className="flex items-center justify-center gap-2">
                        <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                        Adding...
                      </div>
                    ) : (
                      "Add Candidate"
                    )}
                  </button>

                  {isAdded && (
                    <div className="flex items-center gap-2 p-4 bg-green-50 border border-green-200 rounded-lg">
                      <CheckCircle className="w-5 h-5 text-green-600" />
                      <span className="text-green-800 font-medium">Candidate added successfully!</span>
                    </div>
                  )}

                  {addCandidateError && (
                    <div className="flex items-center gap-2 p-4 bg-red-50 border border-red-200 rounded-lg">
                      <AlertCircle className="w-5 h-5 text-red-600" />
                      <span className="text-red-800">{addCandidateError.message}</span>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Stats Footer */}
        <div className="mt-12 bg-white rounded-xl shadow-lg border border-slate-200 p-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 text-center">
            <div className="p-4">
              <div className="text-3xl font-bold text-blue-600 mb-2">
                {candidatesList.length}
              </div>
              <div className="text-slate-600">Total Candidates</div>
            </div>
            <div className="p-4">
              <div className="text-3xl font-bold text-green-600 mb-2">
                {isConnected ? "Connected" : "Not Connected"}
              </div>
              <div className="text-slate-600">Wallet Status</div>
            </div>
            <div className="p-4">
              <div className="text-3xl font-bold text-purple-600 mb-2">
                {isOwner ? "Owner" : "Voter"}
              </div>
              <div className="text-slate-600">Your Role</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}